}

.arrow {
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    position: relative;
    z-index: 10;
}

.arrow:hover {
    background-color: #ddd;
}

.left-arrow {
    margin-right: 10px;
}

.right-arrow {
    margin-left: 10px;
}

.image-container img:hover {
    border-color: #000;
}
.image-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    max-width: 100px;
}

.image-card img {
    width: 100px;
    height: 100px;
    object-fit: cover;
    border-radius: 8px;
    margin-bottom: 8px;
    cursor: pointer;
    border: 2px solid transparent;
    transition: border-color 0.3s ease;
}

.image-card img:hover {
    border-color: #000;
}

/* Container for title and date */
.image-card .info {
    display: flex;
    justify-content: space-between; /* Align title to left and date to right */
    width: 100%; /* Ensure it spans the width of the card */
    font-size: 10px; /* Adjust font size */
    color: #000000;
}

.image-card .title {
    font-weight: 400;
    text-align: left; /* Explicit left alignment */
    margin: 0;
}

.image-card .date {
    text-align: right; /* Explicit right alignment */
    margin: 0;
    color: #000000; /* Make date slightly lighter */
}
